import React, { useState } from 'react'
import { useNavigate } from 'react-router-dom'
import {
  Box,
  Card,
  Container,
  TextField,
  Button,
  Typography,
  Alert,
  Stack,
  InputAdornment,
  IconButton,
  Divider,
  useTheme,
  alpha,
  Fade,
  Zoom,
  Slide,
} from '@mui/material'
import { 
  Visibility, 
  VisibilityOff, 
  Lock as LockIcon, 
  Person as PersonIcon,
  Login as LoginIcon,
  Security as SecurityIcon,
} from '@mui/icons-material'
import { useAuth } from '../../context/AuthContext'
import wuhLogo from '../../assets/wuh_logo.png'

const ModernLoginPage: React.FC = () => {
  const theme = useTheme()
  const [username, setUsername] = useState('')
  const [password, setPassword] = useState('')
  const [showPassword, setShowPassword] = useState(false)
  const [error, setError] = useState('')
  const [loading, setLoading] = useState(false)
  const { login } = useAuth()
  const navigate = useNavigate()

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    setError('')
    setLoading(true)

    try {
      await login(username, password)
      navigate('/dashboard')
    } catch (err: any) {
      setError(
        err.response?.data?.detail || 'Login failed. Please check your credentials.'
      )
    } finally {
      setLoading(false)
    }
  }

  return (
    <Box
      sx={{
        minHeight: '100vh',
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
        background: theme.palette.mode === 'dark'
          ? 'linear-gradient(135deg, rgba(30, 41, 59, 1) 0%, rgba(15, 23, 42, 1) 100%)'
          : 'linear-gradient(135deg, #f5f7fa 0%, #c3cfe2 100%)',
        position: 'relative',
        overflow: 'hidden',
        '&::before': {
          content: '""',
          position: 'absolute',
          top: '-50%',
          left: '-50%',
          width: '200%',
          height: '200%',
          background: `radial-gradient(circle, ${alpha(theme.palette.primary.main, 0.15)} 1px, transparent 1px)`,
          backgroundSize: '50px 50px',
          animation: 'moveBackground 60s linear infinite',
        },
        '@keyframes moveBackground': {
          '0%': { transform: 'translate(0, 0)' },
          '100%': { transform: 'translate(50px, 50px)' },
        },
        '@keyframes float': {
          '0%, 100%': { transform: 'translateY(0px)' },
          '50%': { transform: 'translateY(-20px)' },
        },
        '@keyframes glow': {
          '0%, 100%': { boxShadow: `0 0 20px ${alpha(theme.palette.primary.main, 0.5)}` },
          '50%': { boxShadow: `0 0 40px ${alpha(theme.palette.primary.main, 0.8)}` },
        },
      }}
    >
      {/* Floating Decorative Elements */}
      <Box
        sx={{
          position: 'absolute',
          top: '10%',
          left: '10%',
          width: 100,
          height: 100,
          borderRadius: '50%',
          background: `radial-gradient(circle, ${alpha(theme.palette.primary.main, 0.3)}, transparent)`,
          animation: 'float 6s ease-in-out infinite',
          zIndex: 0,
        }}
      />
      <Box
        sx={{
          position: 'absolute',
          bottom: '15%',
          right: '15%',
          width: 150,
          height: 150,
          borderRadius: '50%',
          background: `radial-gradient(circle, ${alpha(theme.palette.secondary.main, 0.25)}, transparent)`,
          animation: 'float 8s ease-in-out infinite',
          zIndex: 0,
        }}
      />

      <Container maxWidth="sm" sx={{ position: 'relative', zIndex: 1 }}>
        <Fade in timeout={800}>
          <Card
            sx={{
              borderRadius: 4,
              overflow: 'hidden',
              boxShadow: theme.palette.mode === 'dark'
                ? '0 25px 70px rgba(0,0,0,0.5)'
                : '0 25px 70px rgba(0,0,0,0.2)',
              backdropFilter: 'blur(20px)',
              border: `1px solid ${alpha(theme.palette.divider, 0.1)}`,
              background: theme.palette.mode === 'dark'
                ? alpha(theme.palette.background.paper, 0.8)
                : alpha(theme.palette.background.paper, 0.95),
            }}
          >
            {/* Header with Logo */}
            <Box
              sx={{
                background: `linear-gradient(135deg, ${theme.palette.primary.main} 0%, ${theme.palette.primary.dark} 50%, ${theme.palette.secondary.main} 100%)`,
                color: 'white',
                py: 7,
                px: 4,
                textAlign: 'center',
                position: 'relative',
                overflow: 'hidden',
                '&::before': {
                  content: '""',
                  position: 'absolute',
                  top: 0,
                  left: 0,
                  right: 0,
                  bottom: 0,
                  background: 'radial-gradient(circle at 30% 20%, rgba(255,255,255,0.15) 0%, transparent 50%)',
                },
              }}
            >
              <Zoom in timeout={1000}>
                <Box
                  component="img"
                  src={wuhLogo}
                  alt="WUH"
                  sx={{
                    width: 100,
                    height: 100,
                    borderRadius: '50%',
                    mb: 2,
                    border: '5px solid rgba(255,255,255,0.4)',
                    animation: 'glow 3s ease-in-out infinite',
                    position: 'relative',
                    zIndex: 1,
                  }}
                />
              </Zoom>
              <Slide direction="down" in timeout={1200}>
                <Box sx={{ position: 'relative', zIndex: 1 }}>
                  <Typography 
                    variant="h3" 
                    fontWeight={800} 
                    sx={{ 
                      letterSpacing: 1,
                      textShadow: '0 2px 10px rgba(0,0,0,0.2)',
                    }}
                  >
                    WorkSLA Portal
                  </Typography>
                  <Typography 
                    variant="body1" 
                    sx={{ 
                      opacity: 0.95, 
                      mt: 1.5,
                      fontWeight: 500,
                    }}
                  >
                    ระบบรายงานตัวชี้วัด (Open Project - SLA)
                  </Typography>
                  <Typography 
                    variant="caption" 
                    sx={{ 
                      opacity: 0.85, 
                      display: 'block', 
                      mt: 0.5,
                      fontSize: '0.85rem',
                    }}
                  >
                    ศูนย์การแพทย์ มหาวิทยาลัยวลัยลักษณ์
                  </Typography>
                </Box>
              </Slide>
            </Box>

            {/* Form */}
            <Box sx={{ p: 5 }}>
              <form onSubmit={handleSubmit}>
                <Stack spacing={3}>
                  {/* Welcome Text */}
                  <Box sx={{ textAlign: 'center', mb: 2 }}>
                    <Typography 
                      variant="h5" 
                      fontWeight={700} 
                      color="text.primary"
                      gutterBottom
                    >
                      ยินดีต้อนรับ
                    </Typography>
                    <Typography variant="body2" color="text.secondary">
                      กรุณาเข้าสู่ระบบเพื่อดำเนินการต่อ
                    </Typography>
                  </Box>

                  {error && (
                    <Fade in>
                      <Alert 
                        severity="error" 
                        sx={{ 
                          borderRadius: 2,
                          boxShadow: `0 4px 20px ${alpha(theme.palette.error.main, 0.2)}`,
                        }}
                      >
                        {error}
                      </Alert>
                    </Fade>
                  )}
                  
                  <TextField
                    fullWidth
                    label="ชื่อผู้ใช้"
                    placeholder="กรอกชื่อผู้ใช้ของคุณ"
                    value={username}
                    onChange={(e) => setUsername(e.target.value)}
                    disabled={loading}
                    sx={{
                      '& .MuiOutlinedInput-root': {
                        borderRadius: 2,
                        transition: 'all 0.3s',
                        '&:hover': {
                          boxShadow: `0 4px 20px ${alpha(theme.palette.primary.main, 0.1)}`,
                        },
                        '&.Mui-focused': {
                          boxShadow: `0 4px 20px ${alpha(theme.palette.primary.main, 0.2)}`,
                        },
                      },
                    }}
                    InputProps={{
                      startAdornment: (
                        <InputAdornment position="start">
                          <PersonIcon sx={{ fontSize: 22, color: 'primary.main' }} />
                        </InputAdornment>
                      ),
                    }}
                  />

                  <TextField
                    fullWidth
                    label="รหัสผ่าน"
                    type={showPassword ? 'text' : 'password'}
                    placeholder="กรอกรหัสผ่านของคุณ"
                    value={password}
                    onChange={(e) => setPassword(e.target.value)}
                    disabled={loading}
                    sx={{
                      '& .MuiOutlinedInput-root': {
                        borderRadius: 2,
                        transition: 'all 0.3s',
                        '&:hover': {
                          boxShadow: `0 4px 20px ${alpha(theme.palette.primary.main, 0.1)}`,
                        },
                        '&.Mui-focused': {
                          boxShadow: `0 4px 20px ${alpha(theme.palette.primary.main, 0.2)}`,
                        },
                      },
                    }}
                    InputProps={{
                      startAdornment: (
                        <InputAdornment position="start">
                          <LockIcon sx={{ fontSize: 22, color: 'primary.main' }} />
                        </InputAdornment>
                      ),
                      endAdornment: (
                        <InputAdornment position="end">
                          <IconButton
                            onClick={() => setShowPassword(!showPassword)}
                            disabled={loading}
                            size="small"
                            sx={{
                              transition: 'transform 0.2s',
                              '&:hover': {
                                transform: 'scale(1.1)',
                              },
                            }}
                          >
                            {showPassword ? <VisibilityOff /> : <Visibility />}
                          </IconButton>
                        </InputAdornment>
                      ),
                    }}
                  />

                  <Button
                    fullWidth
                    type="submit"
                    size="large"
                    disabled={loading || !username || !password}
                    startIcon={<LoginIcon />}
                    sx={{
                      background: `linear-gradient(135deg, ${theme.palette.primary.main} 0%, ${theme.palette.primary.dark} 50%, ${theme.palette.secondary.main} 100%)`,
                      color: 'white',
                      fontWeight: 700,
                      py: 1.8,
                      borderRadius: 2,
                      fontSize: '1rem',
                      textTransform: 'none',
                      boxShadow: `0 8px 24px ${alpha(theme.palette.primary.main, 0.4)}`,
                      transition: 'all 0.3s cubic-bezier(0.4, 0, 0.2, 1)',
                      '&:hover': {
                        transform: 'translateY(-2px)',
                        boxShadow: `0 12px 32px ${alpha(theme.palette.primary.main, 0.5)}`,
                      },
                      '&:active': {
                        transform: 'translateY(0)',
                      },
                      '&.Mui-disabled': {
                        background: alpha(theme.palette.action.disabled, 0.12),
                        color: theme.palette.action.disabled,
                      },
                    }}
                  >
                    {loading ? 'กำลังเข้าสู่ระบบ...' : 'เข้าสู่ระบบ'}
                  </Button>

                  <Divider sx={{ my: 1 }} />

                  {/* Demo Info */}
                  <Box 
                    sx={{ 
                      p: 3, 
                      borderRadius: 3, 
                      bgcolor: alpha(theme.palette.info.main, 0.08),
                      border: `1px dashed ${alpha(theme.palette.info.main, 0.3)}`,
                      textAlign: 'center',
                    }}
                  >
                    <Stack direction="row" justifyContent="center" alignItems="center" spacing={1} mb={1}>
                      <SecurityIcon sx={{ fontSize: 20, color: 'info.main' }} />
                      <Typography variant="body2" fontWeight={700} color="info.main">
                        ข้อมูลทดสอบ (Demo)
                      </Typography>
                    </Stack>
                    <Typography variant="body2" color="text.secondary">
                      ชื่อผู้ใช้: <Box component="span" fontWeight={700} color="text.primary">admin</Box>
                    </Typography>
                    <Typography variant="body2" color="text.secondary">
                      รหัสผ่าน: <Box component="span" fontWeight={700} color="text.primary">admin123</Box>
                    </Typography>
                  </Box>

                  {/* Footer Note */}
                  <Typography 
                    variant="caption" 
                    color="text.secondary" 
                    textAlign="center"
                    sx={{ mt: 2 }}
                  >
                    ระบบนี้มีการเก็บข้อมูลการใช้งานเพื่อการตรวจสอบความปลอดภัย
                  </Typography>
                </Stack>
              </form>
            </Box>
          </Card>
        </Fade>
      </Container>
    </Box>
  )
}

export default ModernLoginPage
