import React, { useState } from 'react';
import { useQuery, useMutation, useQueryClient } from '@tanstack/react-query';
import {
  Box,
  Typography,
  Paper,
  Tabs,
  Tab,
  Button,
  TextField,
  Grid,
  Card,
  CardContent,
  Switch,
  FormControlLabel,
  MenuItem,
  Divider,
  Chip,
  Stack,
  Alert,
  IconButton,
  Tooltip,
  List,
  ListItem,
  ListItemText,
  ListItemSecondaryAction,
  CircularProgress,
} from '@mui/material';
import {
  Settings,
  FilterList,
  Sync,
  Api,
  Palette,
  Save,
  Refresh,
  CheckCircle,
  Error as ErrorIcon,
  Schedule,
  DarkMode,
  LightMode,
} from '@mui/icons-material';
import { LoadingState, ErrorState } from '../../components/ui';
import { useTheme } from '../../context/ThemeContext';
import { format } from 'date-fns';
import { th } from 'date-fns/locale';
import { normalizeAssignees, getArray, safeMap, type Assignee } from '../../schemas/adminSettings';

interface TabPanelProps {
  children?: React.ReactNode;
  index: number;
  value: number;
}

function TabPanel(props: TabPanelProps) {
  const { children, value, index, ...other } = props;
  return (
    <div role="tabpanel" hidden={value !== index} {...other}>
      {value === index && <Box sx={{ p: 3 }}>{children}</Box>}
    </div>
  );
}

const SettingsPage: React.FC = () => {
  const [tabValue, setTabValue] = useState(0);
  const queryClient = useQueryClient();
  const { mode, toggleTheme } = useTheme();

  // Tab 1: Default Filters State
  const [defaultFilters, setDefaultFilters] = useState({
    created_from: '',
    created_to: '',
    assignees: [] as string[],
  });

  // Tab 2: Sync Options State
  const [syncOptions, setSyncOptions] = useState({
    autoSync: false,
    syncInterval: 60, // minutes
  });
  const [lastSyncTime, setLastSyncTime] = useState<Date | null>(null);
  const [isSyncing, setIsSyncing] = useState(false);

  // Tab 3: API Routes State
  const apiRoutes = [
    { name: 'Work Packages List', endpoint: '/api/work-packages', method: 'GET' },
    { name: 'Work Package Detail', endpoint: '/api/work-packages/:id', method: 'GET' },
    { name: 'Work Package Journals', endpoint: '/api/work-packages/:id/journals', method: 'GET' },
    { name: 'Dashboard Stats', endpoint: '/api/workpackages/dashboard', method: 'GET' },
    { name: 'Admin Assignees', endpoint: '/api/admin/assignees', method: 'GET' },
    { name: 'Sync Work Packages', endpoint: '/api/admin/workpackages-sync', method: 'POST' },
  ];
  const [apiStatus, setApiStatus] = useState<Record<string, { status: 'checking' | 'success' | 'error'; time?: number }>>({});

  // Tab 4: UI Customization State
  const [uiSettings, setUiSettings] = useState({
    sidebarExpanded: true,
    fontSize: 14,
    showBreadcrumbs: true,
  });

  const handleTabChange = (_event: React.SyntheticEvent, newValue: number) => {
    setTabValue(newValue);
  };

  // Load Default Filters
  const { data: filtersData, isLoading: isFiltersLoading } = useQuery({
    queryKey: ['admin-filters'],
    queryFn: async () => {
      const response = await fetch('/worksla/api/admin/workpackages-filters', {
        credentials: 'include',
      });
      if (!response.ok) throw new Error('Failed to load filters');
      return response.json();
    },
  });

  React.useEffect(() => {
    if (filtersData) {
      setDefaultFilters({
        created_from: filtersData.created_from || '',
        created_to: filtersData.created_to || '',
        assignees: filtersData.assignees || [],
      });
    }
  }, [filtersData]);

  // Load Assignees for filter dropdown
  const { data: assigneesData, isLoading: isAssigneesLoading } = useQuery({
    queryKey: ['admin-assignees'],
    queryFn: async () => {
      const response = await fetch('/worksla/api/admin/assignees', {
        credentials: 'include',
      });
      if (!response.ok) throw new Error('Failed to load assignees');
      const data = await response.json();
      console.debug('[settings] assigneesData raw response:', data);
      return data;
    },
  });

  // Normalize assignees data safely
  const normalizedAssignees: Assignee[] = React.useMemo(() => {
    const result = normalizeAssignees(assigneesData);
    console.debug('[settings] normalizedAssignees:', result);
    return result;
  }, [assigneesData]);

  // Save Default Filters Mutation
  const saveFiltersMutation = useMutation({
    mutationFn: async (filters: typeof defaultFilters) => {
      const response = await fetch('/worksla/api/admin/workpackages-filters', {
        method: 'PUT',
        headers: { 'Content-Type': 'application/json' },
        credentials: 'include',
        body: JSON.stringify(filters),
      });
      if (!response.ok) throw new Error('Failed to save filters');
      return response.json();
    },
    onSuccess: () => {
      queryClient.invalidateQueries({ queryKey: ['admin-filters'] });
    },
  });

  // Sync Work Packages Mutation
  const syncMutation = useMutation({
    mutationFn: async () => {
      setIsSyncing(true);
      const response = await fetch('/worksla/api/admin/workpackages-sync', {
        method: 'POST',
        credentials: 'include',
      });
      if (!response.ok) throw new Error('Sync failed');
      return response.json();
    },
    onSuccess: () => {
      setLastSyncTime(new Date());
      setIsSyncing(false);
      queryClient.invalidateQueries({ queryKey: ['workpackages'] });
    },
    onError: () => {
      setIsSyncing(false);
    },
  });

  // Test API Route
  const testApiRoute = async (endpoint: string, method: string) => {
    setApiStatus((prev) => ({ ...prev, [endpoint]: { status: 'checking' } }));
    const startTime = Date.now();

    try {
      // Replace :id with test ID for dynamic routes
      const testEndpoint = endpoint.replace(':id', '34909');
      const url = `/worksla${testEndpoint}`;
      
      const response = await fetch(url, {
        method,
        credentials: 'include',
      });

      const time = Date.now() - startTime;
      setApiStatus((prev) => ({
        ...prev,
        [endpoint]: { status: response.ok ? 'success' : 'error', time },
      }));
    } catch (error) {
      const time = Date.now() - startTime;
      setApiStatus((prev) => ({
        ...prev,
        [endpoint]: { status: 'error', time },
      }));
    }
  };

  // Test All API Routes
  const testAllRoutes = async () => {
    for (const route of apiRoutes) {
      await testApiRoute(route.endpoint, route.method);
    }
  };

  // Render Tab 1: Default Filters
  const renderDefaultFiltersTab = () => {
    if (isFiltersLoading) {
      return <LoadingState message="กำลังโหลดการตั้งค่าตัวกรอง..." />;
    }

    return (
      <Grid container spacing={3}>
        <Grid item xs={12}>
          <Typography variant="h6" gutterBottom sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
            <FilterList color="primary" />
            ตัวกรองเริ่มต้นสำหรับ Work Packages
          </Typography>
          <Typography variant="body2" color="text.secondary" gutterBottom>
            ตั้งค่าตัวกรองเริ่มต้นที่จะใช้เมื่อโหลดหน้า Work Packages
          </Typography>
        </Grid>

        <Grid item xs={12} md={6}>
          <TextField
            fullWidth
            label="วันที่สร้าง (เริ่มต้น)"
            type="date"
            value={defaultFilters.created_from}
            onChange={(e) => setDefaultFilters({ ...defaultFilters, created_from: e.target.value })}
            InputLabelProps={{ shrink: true }}
          />
        </Grid>

        <Grid item xs={12} md={6}>
          <TextField
            fullWidth
            label="วันที่สร้าง (สิ้นสุด)"
            type="date"
            value={defaultFilters.created_to}
            onChange={(e) => setDefaultFilters({ ...defaultFilters, created_to: e.target.value })}
            InputLabelProps={{ shrink: true }}
          />
        </Grid>

        <Grid item xs={12}>
          <TextField
            fullWidth
            select
            label="ผู้รับผิดชอบ"
            value={Array.isArray(defaultFilters.assignees) ? defaultFilters.assignees : []}
            onChange={(e) => {
              const value = e.target.value;
              setDefaultFilters({ 
                ...defaultFilters, 
                assignees: Array.isArray(value) ? value : []
              });
            }}
            SelectProps={{ multiple: true }}
            helperText="เลือกผู้รับผิดชอบหลายคนได้"
            disabled={isAssigneesLoading}
          >
            {normalizedAssignees.length > 0 ? (
              normalizedAssignees.map((assignee) => (
                <MenuItem key={assignee.id} value={assignee.id.toString()}>
                  {assignee.name}
                </MenuItem>
              ))
            ) : (
              <MenuItem disabled value="">
                {isAssigneesLoading ? 'กำลังโหลด...' : 'ไม่มีข้อมูลผู้รับผิดชอบ'}
              </MenuItem>
            )}
          </TextField>
        </Grid>

        <Grid item xs={12}>
          <Box display="flex" gap={2}>
            <Button
              variant="contained"
              startIcon={<Save />}
              onClick={() => saveFiltersMutation.mutate(defaultFilters)}
              disabled={saveFiltersMutation.isPending}
            >
              บันทึกการตั้งค่า
            </Button>
            {saveFiltersMutation.isSuccess && (
              <Alert severity="success" sx={{ py: 0 }}>
                บันทึกสำเร็จ!
              </Alert>
            )}
            {saveFiltersMutation.isError && (
              <Alert severity="error" sx={{ py: 0 }}>
                เกิดข้อผิดพลาดในการบันทึก
              </Alert>
            )}
          </Box>
        </Grid>
      </Grid>
    );
  };

  // Render Tab 2: Sync Options
  const renderSyncOptionsTab = () => {
    return (
      <Grid container spacing={3}>
        <Grid item xs={12}>
          <Typography variant="h6" gutterBottom sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
            <Sync color="primary" />
            ตัวเลือกการซิงค์ข้อมูล
          </Typography>
          <Typography variant="body2" color="text.secondary" gutterBottom>
            จัดการการซิงค์ข้อมูล Work Packages จาก OpenProject
          </Typography>
        </Grid>

        <Grid item xs={12}>
          <Card variant="outlined">
            <CardContent>
              <Typography variant="subtitle1" fontWeight={600} gutterBottom>
                ซิงค์ข้อมูลทันที
              </Typography>
              <Typography variant="body2" color="text.secondary" gutterBottom>
                ดึงข้อมูล Work Packages ล่าสุดจาก OpenProject
              </Typography>
              <Box mt={2}>
                <Button
                  variant="contained"
                  startIcon={isSyncing ? <CircularProgress size={20} color="inherit" /> : <Sync />}
                  onClick={() => syncMutation.mutate()}
                  disabled={isSyncing}
                  size="large"
                >
                  {isSyncing ? 'กำลังซิงค์...' : 'ซิงค์ตอนนี้'}
                </Button>
              </Box>
              {lastSyncTime && (
                <Box mt={2}>
                  <Chip
                    icon={<Schedule />}
                    label={`ซิงค์ล่าสุด: ${format(lastSyncTime, 'dd MMM yyyy HH:mm', { locale: th })}`}
                    color="success"
                    variant="outlined"
                  />
                </Box>
              )}
            </CardContent>
          </Card>
        </Grid>

        <Grid item xs={12}>
          <Card variant="outlined">
            <CardContent>
              <FormControlLabel
                control={
                  <Switch
                    checked={syncOptions.autoSync}
                    onChange={(e) => setSyncOptions({ ...syncOptions, autoSync: e.target.checked })}
                  />
                }
                label="เปิดใช้งานการซิงค์อัตโนมัติ"
              />
              <Typography variant="body2" color="text.secondary" sx={{ mt: 1, mb: 2 }}>
                ระบบจะซิงค์ข้อมูลอัตโนมัติตามช่วงเวลาที่กำหนด
              </Typography>

              {syncOptions.autoSync && (
                <TextField
                  select
                  label="ช่วงเวลาการซิงค์"
                  value={syncOptions.syncInterval}
                  onChange={(e) => setSyncOptions({ ...syncOptions, syncInterval: Number(e.target.value) })}
                  sx={{ minWidth: 200 }}
                >
                  <MenuItem value={15}>ทุก 15 นาที</MenuItem>
                  <MenuItem value={30}>ทุก 30 นาที</MenuItem>
                  <MenuItem value={60}>ทุก 1 ชั่วโมง</MenuItem>
                  <MenuItem value={120}>ทุก 2 ชั่วโมง</MenuItem>
                  <MenuItem value={360}>ทุก 6 ชั่วโมง</MenuItem>
                  <MenuItem value={1440}>ทุก 24 ชั่วโมง</MenuItem>
                </TextField>
              )}
            </CardContent>
          </Card>
        </Grid>
      </Grid>
    );
  };

  // Render Tab 3: API Routes Check
  const renderApiRoutesTab = () => {
    return (
      <Grid container spacing={3}>
        <Grid item xs={12}>
          <Box display="flex" justifyContent="space-between" alignItems="center">
            <Box>
              <Typography variant="h6" gutterBottom sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
                <Api color="primary" />
                ตรวจสอบ API Routes
              </Typography>
              <Typography variant="body2" color="text.secondary">
                ทดสอบการเชื่อมต่อกับ API endpoints ทั้งหมด
              </Typography>
            </Box>
            <Button variant="contained" startIcon={<Refresh />} onClick={testAllRoutes}>
              ทดสอบทั้งหมด
            </Button>
          </Box>
        </Grid>

        <Grid item xs={12}>
          <List>
            {apiRoutes.map((route, index) => {
              const status = apiStatus[route.endpoint];
              return (
                <React.Fragment key={route.endpoint}>
                  <ListItem>
                    <ListItemText
                      primary={
                        <Box display="flex" alignItems="center" gap={1}>
                          <Chip label={route.method} size="small" color="primary" sx={{ minWidth: 60 }} />
                          <Typography variant="body1" fontWeight={500}>
                            {route.name}
                          </Typography>
                        </Box>
                      }
                      secondary={route.endpoint}
                    />
                    <ListItemSecondaryAction>
                      <Box display="flex" alignItems="center" gap={1}>
                        {status && status.time && (
                          <Chip label={`${status.time}ms`} size="small" variant="outlined" />
                        )}
                        {status?.status === 'checking' && <CircularProgress size={24} />}
                        {status?.status === 'success' && (
                          <Chip
                            icon={<CheckCircle />}
                            label="OK"
                            size="small"
                            color="success"
                          />
                        )}
                        {status?.status === 'error' && (
                          <Chip
                            icon={<ErrorIcon />}
                            label="Error"
                            size="small"
                            color="error"
                          />
                        )}
                        <IconButton
                          size="small"
                          onClick={() => testApiRoute(route.endpoint, route.method)}
                        >
                          <Refresh fontSize="small" />
                        </IconButton>
                      </Box>
                    </ListItemSecondaryAction>
                  </ListItem>
                  {index < apiRoutes.length - 1 && <Divider />}
                </React.Fragment>
              );
            })}
          </List>
        </Grid>
      </Grid>
    );
  };

  // Render Tab 4: UI Customization
  const renderUiCustomizationTab = () => {
    return (
      <Grid container spacing={3}>
        <Grid item xs={12}>
          <Typography variant="h6" gutterBottom sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
            <Palette color="primary" />
            ปรับแต่ง UI
          </Typography>
          <Typography variant="body2" color="text.secondary" gutterBottom>
            ตั้งค่าการแสดงผลของระบบ
          </Typography>
        </Grid>

        <Grid item xs={12} md={6}>
          <Card variant="outlined">
            <CardContent>
              <Typography variant="subtitle1" fontWeight={600} gutterBottom>
                ธีมสี
              </Typography>
              <Stack direction="row" spacing={2} alignItems="center">
                <Button
                  variant={mode === 'light' ? 'contained' : 'outlined'}
                  startIcon={<LightMode />}
                  onClick={() => mode === 'dark' && toggleTheme()}
                  fullWidth
                >
                  โหมดสว่าง
                </Button>
                <Button
                  variant={mode === 'dark' ? 'contained' : 'outlined'}
                  startIcon={<DarkMode />}
                  onClick={() => mode === 'light' && toggleTheme()}
                  fullWidth
                >
                  โหมดมืด
                </Button>
              </Stack>
            </CardContent>
          </Card>
        </Grid>

        <Grid item xs={12} md={6}>
          <Card variant="outlined">
            <CardContent>
              <Typography variant="subtitle1" fontWeight={600} gutterBottom>
                ขนาดตัวอักษร
              </Typography>
              <TextField
                select
                fullWidth
                value={uiSettings.fontSize}
                onChange={(e) => setUiSettings({ ...uiSettings, fontSize: Number(e.target.value) })}
              >
                <MenuItem value={12}>เล็ก (12px)</MenuItem>
                <MenuItem value={14}>ปกติ (14px)</MenuItem>
                <MenuItem value={16}>ใหญ่ (16px)</MenuItem>
                <MenuItem value={18}>ใหญ่มาก (18px)</MenuItem>
              </TextField>
            </CardContent>
          </Card>
        </Grid>

        <Grid item xs={12}>
          <Card variant="outlined">
            <CardContent>
              <Typography variant="subtitle1" fontWeight={600} gutterBottom>
                การแสดงผล
              </Typography>
              <Stack spacing={2}>
                <FormControlLabel
                  control={
                    <Switch
                      checked={uiSettings.sidebarExpanded}
                      onChange={(e) => setUiSettings({ ...uiSettings, sidebarExpanded: e.target.checked })}
                    />
                  }
                  label="แสดง Sidebar แบบขยายเป็นค่าเริ่มต้น"
                />
                <FormControlLabel
                  control={
                    <Switch
                      checked={uiSettings.showBreadcrumbs}
                      onChange={(e) => setUiSettings({ ...uiSettings, showBreadcrumbs: e.target.checked })}
                    />
                  }
                  label="แสดง Breadcrumbs"
                />
              </Stack>
            </CardContent>
          </Card>
        </Grid>

        <Grid item xs={12}>
          <Button variant="contained" startIcon={<Save />}>
            บันทึกการตั้งค่า UI
          </Button>
        </Grid>
      </Grid>
    );
  };

  return (
    <Box sx={{ p: 3 }}>
      <Typography variant="h4" fontWeight={700} gutterBottom sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
        <Settings fontSize="large" color="primary" />
        การตั้งค่าระบบ
      </Typography>
      <Typography variant="body2" color="text.secondary" gutterBottom sx={{ mb: 3 }}>
        จัดการการตั้งค่าต่างๆ ของระบบ WorkSLA
      </Typography>

      <Paper elevation={2} sx={{ borderRadius: 2 }}>
        <Tabs
          value={tabValue}
          onChange={handleTabChange}
          variant="scrollable"
          scrollButtons="auto"
          sx={{ borderBottom: 1, borderColor: 'divider' }}
        >
          <Tab icon={<FilterList />} iconPosition="start" label="ตัวกรองเริ่มต้น" />
          <Tab icon={<Sync />} iconPosition="start" label="การซิงค์ข้อมูล" />
          <Tab icon={<Api />} iconPosition="start" label="API Routes" />
          <Tab icon={<Palette />} iconPosition="start" label="UI Customization" />
        </Tabs>

        <TabPanel value={tabValue} index={0}>
          {renderDefaultFiltersTab()}
        </TabPanel>
        <TabPanel value={tabValue} index={1}>
          {renderSyncOptionsTab()}
        </TabPanel>
        <TabPanel value={tabValue} index={2}>
          {renderApiRoutesTab()}
        </TabPanel>
        <TabPanel value={tabValue} index={3}>
          {renderUiCustomizationTab()}
        </TabPanel>
      </Paper>
    </Box>
  );
};

export default SettingsPage;
